import React, {useEffect, useState, useRef} from 'react'
import {Canvas, useThree, useFrame} from '@react-three/fiber'
import { Float, OrbitControls, PerspectiveCamera  } from '@react-three/drei'
import { SceneMap } from '../3dmodels/Scene'
import { useSpring, a } from '@react-spring/three'
import * as THREE from "three";

import '../3dmodels/models.scss'

function Box(){
    return(
        <mesh>
            <boxGeometry  attach="geometry"/>
            <meshStandardMaterial  attach="material" color=""/>
        </mesh>
    )
}



function Test(props){
    const {camReset, setcamReset} = props;
    const camRef = useRef()
    const vec = new THREE.Vector3();

    const [posX, setposX] = useState({pos: 0, is: true})
    const [posY, setposY] = useState({pos: 0, is: true})
    const [posZ, setposZ] = useState({pos: 0, is: true})

    const [rotX, setrotX] = useState({rot: 0, is: true})
    const [rotY, setrotY] = useState({rot: 0, is: true})
    const [rotZ, setrotZ] = useState({rot: 0, is: true})
    

    function simpleReset(){
        const eps = 0.00001;
        var isEps = false;


        if(Math.abs(camRef.current.position.x - posX.pos) < eps
            && Math.abs(camRef.current.position.y - posY.pos) < eps
            && Math.abs(camRef.current.position.z - posZ.pos) < eps
        ){
            setcamReset(isEps)

            console.log("END OF RESET")
        }
    
    }

    useFrame((state)=>{
        
        if(!camRef.current) return;


        // console.log(`pos: 
        //     ${state.camera.position.x.toFixed(5)}  
        //     ${state.camera.position.y.toFixed(5)}  
        //     ${state.camera.position.z.toFixed(5)}`)
        // console.log(`rot: 
        //     ${state.camera.rotation.x.toFixed(5)}  
        //     ${state.camera.rotation.y.toFixed(5)}  
        //     ${state.camera.rotation.z.toFixed(5)}`)
         
        
    // if(state.camera.position.x > 1) state.camera.position.x -= 0.07


        if(state.camera.position.x > 1.5) state.camera.position.x -= 0.05
        if(state.camera.position.x > 0.6) state.camera.position.x -= 0.01
        if(state.camera.position.x < -1.5) state.camera.position.x += 0.05
        if(state.camera.position.x < -0.6) state.camera.position.x += 0.01
        
        if(state.camera.position.y > 1.5) state.camera.position.y -= 0.05
        if(state.camera.position.y > 0.6) state.camera.position.y -= 0.01
        if(state.camera.position.y < -1.5) state.camera.position.y += 0.05
        if(state.camera.position.y < -0.6) state.camera.position.y += 0.01


        if(state.camera.position.z < 0) state.camera.position.z += 0.07
        if(state.camera.position.z < 1) state.camera.position.z += 0.01
        if(state.camera.position.z > 3) state.camera.position.z -= 0.05
      

        if(camReset){

            state.camera.rotation.x = 0;
            state.camera.rotation.y = 0;
            state.camera.rotation.z = 0;

            state.camera.position.lerp(vec.set(0, 0, 3), .1)

            simpleReset()

        }
        else{
            state.camera.rotation.x = 0;
            state.camera.rotation.y = 0;
            state.camera.rotation.z = 0;
        }

        state.camera.updateProjectionMatrix()
    })

    useEffect(()=>{
        if(camRef.current){
            setposX({pos:camRef.current.position.x, is: true})
            setposY({pos:camRef.current.position.y, is: true})
            setposZ({pos:camRef.current.position.z, is: true})

            setrotX({rot:camRef.current.rotation.x, is: true})
            setrotY({rot:camRef.current.rotation.y, is: true})
            setrotZ({rot:camRef.current.rotation.z, is: true})

            console.log("start", camRef.current.rotation.z)
        }
        
    }, [])

    return(
        <PerspectiveCamera ref={camRef} makeDefault position={[0,0,3]}/>
    )
}

