import React from 'react'
import '../header/header.scss'

function Header() {
  return (
    <div className="headerSection">
      <div className='headerSection_wrapper'>
        <div className="headerSection_left">
          <div className="headerSection_logo">uc</div>

          <div className="headerSection_nav">
            <div className="headerSection_nav_item headerSection_nav_item-active">map</div>
            <div className="headerSection_nav_item">history</div>
            <div className="headerSection_nav_item">art</div>
            <div className="headerSection_nav_item">fation</div>
          </div>
        </div>

        <div className="headerSection_right">
          <input className='headerSection_input'></input>
          <button className='headerSection_themeBtn'></button>
        </div>
      </div>
    </div>
  )
}

export default Header
