
import '../3dmodels/scene.scss'

import React, { useRef, useState } from 'react'
import { useGLTF } from '@react-three/drei'
import {proxy} from 'valtio'
import { useSpring, a } from '@react-spring/three'
const path = '/usaMap/scene.gltf';




export function SceneMap(props) {
  const {camReset, setcamReset} = props;

  const { nodes, materials } = useGLTF(path)
  const [cState, setcState] = useState(null)

  // const actionState = proxy({
  //   current: null,
  //   items:{
  //     s_1: useSpring({pos: [-1.08, 2.51, 0]}),
  //   }
  // })
  const toPlace = [-1.1, 1.55, 2.8];
  // const toPlace = [0, 0, 1];
  const sh_1 = useSpring({pos: cState == 's_1' ?  toPlace: [-1.08, 2.51, 0]})
  const sh_2 = useSpring({pos: cState == 's_2' ?  toPlace : [-0.05, 2.46, 0]})
  const sh_3 = useSpring({pos: cState == 's_3' ?  toPlace : [-1.13, 1.82, 0]})
  const sh_4 = useSpring({pos: cState == 's_4' ?  toPlace : [-2.26, 1.77, 0]})
  const sh_5 = useSpring({pos: cState == 's_5' ?  toPlace : [-1.18, 1.17, 0]})
  const sh_6 = useSpring({pos: cState == 's_6' ?  toPlace : [0.02, 1.24, 0]})
  const sh_7 = useSpring({pos: cState == 's_7' ?  toPlace : [-2.09, 0.81, 0]})
  const sh_8 = useSpring({pos: cState == 's_8' ?  toPlace : [-0.9, 0.46, 0]})

  const cs_1 = {col: cState == 's_1' ? "#433432" : "#923"}

  
  return (
    <group 
    {...props} 
    dispose={null} 
    rotation={[0, 0, 0]} 
    position={[0,-0.5,0]}
    >
      <group rotation={[0, 0, 0]} scale={0.52}
      
      onPointerDown={(e)=>{
        e.stopPropagation() 
        
        const shit = e.object.material.name;
        console.log(shit)
        if(shit === cState) setcState(null)
        else setcState(shit)


        camReset ? setcamReset(false) : setcamReset(true)
      }}

      
      >
       <a.group position={sh_7.pos}>
          <mesh geometry={nodes.Colorado_0.geometry} material={materials.s_7} />
        </a.group>
        <a.group position={sh_6}>
          <mesh geometry={nodes.Iowa_0.geometry} material={materials.s_6} />
        </a.group>
        <a.group position={sh_8.pos}>
          <mesh geometry={nodes.Kansas_0.geometry} material={materials.s_8} />
        </a.group >
        <a.group position={sh_2.pos}>
          <mesh geometry={nodes.Minnesota_0.geometry} material={materials.s_2} />
        </a.group>
        <a.group position={[0.18, 0.56, 0]}>
          <mesh geometry={nodes.Missouri_0.geometry} material={materials.s_9} />
        </a.group>
        <a.group position={sh_5.pos}>
          <mesh geometry={nodes.Nebraska_0.geometry} material={materials.s_5} />
        </a.group>
        <a.group position={sh_1.pos}>
          <mesh geometry={nodes.North_dakota_0.geometry} material={materials.s_1}  material-color={cs_1.col}/>
        </a.group>
        <a.group position={sh_3.pos}>
          <mesh geometry={nodes.South_Dakota_0.geometry} material={materials.s_3} />
        </a.group>
        <a.group position={sh_4.pos}>
          <mesh geometry={nodes.Wyoming_0.geometry} material={materials.s_4} />
        </a.group>
        
      </group>
    </group>
  )
}

useGLTF.preload(path)
